@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.nav-container{width:100%;height:auto;background-color:rgba(0,0,0,0.3);padding:3px 0}.nav-container ul{display:flex;list-style:none;padding:2px;margin:0}.nav-container li{width:auto;height:28px;padding:0 5px;margin:0 2px;border-radius:3px;display:flex;align-items:center;background-color:rgba(233,226,226,0.3);color:#fff;font-weight:500}li a{text-decoration:none}li a:visited{color:#fff}li:hover{background-color:#909394}

.board-container{width:100%;height:95.8vh;color:#142541;overflow-x:scroll}.board-name{box-sizing:border-box;display:flex;justify-content:center;margin:5px 0;height:30px;align-items:center;background-color:rgba(255,255,255,0.3);font-weight:500}.inner_board-container{display:flex;width:100%}

.inner_toDo-container{display:flex;flex-direction:column}.inner_toDo-container span{box-sizing:border-box;display:flex;align-items:center;margin:5px;background-color:#fff;border-radius:3px;box-shadow:0 1px 0 1px silver;cursor:pointer;height:32px;padding-left:5px;font-size:15px}.inner_toDo-container span:hover{background-color:#dadce3}button{margin:5px;border:none;min-width:230px;height:45px;border-radius:3px;font-family:sans-serif, 'Courier New';font-size:15px;background-color:#d4d8dd;cursor:pointer}.new-column{margin:0}button:hover{background-color:#dadce3}.new-column:hover{background-color:#c1c9cb}button:active,button:focus{outline:none;border:none}

.toDo-container{margin:0 5px;padding:0 2px;background-color:#ebecf0;border-radius:3px;box-sizing:border-box;display:flex;flex-direction:column;min-width:250px;min-height:150px;position:relative;white-space:normal}.toDo-container h2{display:flex;text-align:center;padding-left:5px;font-size:18px;line-height:18px;margin:5px 0;height:30px;align-items:center;font-weight:500}

a{text-decoration:none}ul{text-decoration:none;list-style-type:none;margin-top:5px;padding:0}.home-container{width:100%;height:100vh;display:flex;flex-direction:column;align-items:center;font-size:18px;color:#597985}.home_board-container{max-width:1024px;height:auto}.main-text{margin:8px}.home-container ul{font-family:'Roboto', sans-serif;letter-spacing:1px;display:flex;flex-wrap:wrap}.home-container li{width:220px;height:120px;background-color:rgba(9,30,66,0.04);color:#fff;margin:6px;border-radius:5px;padding:10px;font-size:16px;font-weight:500}li div{height:100%;display:flex;text-align:center;justify-content:center;align-items:center;color:#172b4d;font-weight:600}.App-link{color:#61dafb}li:hover{cursor:pointer;opacity:0.9}

